import { Grid, Typography } from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom'

export const DashboardGallery = () => {
  const lang = useSelector((state)=>state.value);
  const section = {
    title:{
      eng:'OUR GALLERY',
      kan:' '
    },
    main:{
      eng:'Unforgettable Memories with AGRIVIMAAN',
      kan:'   '
    },
    sub:{
      eng:'Explore our captivating agriculture drone gallery, showcasing the beauty and efficiency of drone technology in modern farming.',
      kan:'             .'
    },
    btn:{
      eng:'VIEW GALLERY',
      kan:' '
    }
  }
  return (
    <div className='pb-5'>
      <Grid display={'flex'} justifyContent={'center'} minHeight={150} >
        <Grid >
          {/* <img src="assets/element-blur-2 1.webp" alt="" className='position-absolute z-1' style={{ marginTop: -100, marginLeft: -160 }} /> */}
          <img src="assets/drone_camera.webp" alt="" className='position-absolute z-2' style={{ marginTop: 0, marginLeft: -80 }} />
        </Grid>
      </Grid>
      <Grid marginY={5}>
        <Typography textAlign={'center'} className='ff-mi fw-bold' variant='h6' marginTop={'5'} color={'#1D69E7'}>{section.title[lang]}</Typography>
        <Typography textAlign={'center'} className='ff-mi' variant='h4' color={'white'} marginY={2}>{section.main[lang]}</Typography>
        {/* <img src="assets/element-blur-2 1.webp" alt="" className='blur-section-4' /> */}
        <Typography textAlign={'center'} className='ff-mo' color={'white'}>{section.sub[lang]}</Typography>
      </Grid>
      <Grid container marginTop={5}>
        {[...Array(1)].map((el, i) => (
          <Grid key={'g' + i} padding={1} item xs={12} sm={6} md={4} lg={3}>
            <img src="assets/Frame_10.webp" width={'100%'} alt="" />
          </Grid>
        ))}
      </Grid>
      <Grid display={'flex'} justifyContent={'center'} >
        <Link to={'/gallery'}>
          <button className='btn btn-primary rounded-1 py-2 px-4 ff-mo bg-btn-g shadow' >{section.btn[lang]}</button>
        </Link>
      </Grid>
    </div>
  )
}
