import { CheckCircle } from '@mui/icons-material'
import { Grid, Typography } from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

export const DashboardOurTeam = () => {
    const lang = useSelector((state)=>state.value);
    const section = {
        title:{
            eng:'OUR TEAM',
            kan:' '
        },
        main:{
            eng:'Our Experienced Pilots',
            kan:'  '
        },
        sub:{
            eng:'Our experienced pilots bring expertise and professionalism to every agriculture drone mission.',
            kan:'          .'
        },
        p1:{
            eng:'In-depth knowledge of drone technology and agriculture practices',
            kan:'      '
        },
        p2:{
            eng:'Precise and efficient operations.',
            kan:'   .'
        },
        p3:{
            eng:'Deliver accurate data and optimal results for your farming needs.',
            kan:'        .'
        },
        btn:{
            eng:'JOIN OUR TEAM',
            kan:'  '
        }
    }
    return (
        <div className='py-5'>
            <Grid container>
                <Grid container item sm={12} md={6} display={'flex'} justifyContent={'center'}>
                    <Grid container item sm={12} md={9}>
                        {[...Array(1)].map((el, i) => (
                            <Grid key={'t' + i} padding={1} item xs={6}>
                                <img src="assets/Frame_18.webp" width={'100%'} alt="" />
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
                <Grid item sm={12} md={6}>
                    <Typography className='ff-mi fw-bold' variant='h6' marginTop={'5'} color={'#1D69E7'}>{section.title[lang]}</Typography>
                    <Typography className='ff-mi' variant='h4' marginY={2}>{section.main[lang]}</Typography>
                    {/* <img src="assets/element-blur-2 1.webp" alt="" className='blur-section-6' /> */}
                    <Typography className='ff-mo' color={'#656565'}>{section.sub[lang]}</Typography>
                    <Typography className='ff-mo ts-14 mb-2 mt-5' color={'#656565'}>
                        <CheckCircle color='primary' fontSize='small' /> {section.p1[lang]}
                    </Typography>
                    <Typography className='ff-mo ts-14 my-2' color={'#656565'}>
                        <CheckCircle color='primary' fontSize='small' /> {section.p2[lang]}
                    </Typography>
                    <Typography className='ff-mo ts-14 my-2' color={'#656565'}>
                        <CheckCircle color='primary' fontSize='small' /> {section.p3[lang]}
                    </Typography>
                    <Grid display={'flex'} justifyContent={'start'} mt={5}>
                        <Link to={'/contact?dest=form'}>
                            <button className='btn btn-primary rounded-1 py-2 px-4 ff-mo bg-btn-g shadow' >{section.btn[lang]}</button>
                        </Link>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    )
}
