import { Grid, Typography } from '@mui/material'
// import { Animation } from 'react-animate-style'
import React from 'react'
import { useSelector } from 'react-redux';

export const DashboardExpertise = () => {
    const lang = useSelector((state)=>state.value);
    const heading = {
        eng:'Our Expertise',
        kan:' '
    }
    const subHeading = {
        eng:'We Are Different & Always Professional',
        kan:'    '
    }
    const section = {
        main1:{
            eng:'Drone piloting',
            kan:' '
        },
        sub1:{
            eng:'The ability to fly a drone safely and effectively.',
            kan:'      .'
        },
        main2:{
            eng:'Crop science',
            kan:' '
        },
        sub2:{
            eng:'The knowledge of crop growth and production.',
            kan:'    .'
        },
        main3:{
            eng:'Data analysis',
            kan:' '
        },
        sub3:{
            eng:'The ability to use data collected by drones to make informed decisions about crop management.',
            kan:'          .'
        },
        main4:{
            eng:'Business development',
            kan:' '
        },
        sub4:{
            eng:'The ability to identify and develop new markets for agricultural drones.',
            kan:'       .'
        }
    }
    const section2 = {
        main:{
            eng:'Explore Your World Without Limitations',
            kan:'   '
        },
        sub:{
            eng:'Precision mapping, crop health insights, efficient resource management, time-saving efficiency, data-driven decisions, maximizing yields, and unlocking new possibilities.',
            kan:' ,   ,   ,   , - ,       .'
        }
    }
    return (
        <div>
            <Grid container display="flex" marginY="100px" justifyContent="space-between">
                <Grid item xs={12} md={4} >
                    <Grid display={'flex'} justifyContent={'end'} >
                        <Grid item xs={12} md={9}>
                            {/* <Animation isVisible animationIn='fadeInDown'> */}
                            <Typography className='ff-mi fw-bold' variant='h6' color={'#1D69E7'}>{heading[lang]}</Typography>
                            {/* </Animation> */}
                            {/* <Animation animationInDuration={2000} isVisible animationIn='fadeInLeft' animationInDelay={2000}> */}
                            <Typography className='ff-mi mt-2' variant='h4'>{subHeading[lang]}
                            </Typography>
                            {/* </Animation> */}
                            {/* <div className='d-flex justify-content-start' style={{ marginTop: -120 }}>
                                <img src="assets/element-blur-2 1.webp" alt="" height={150} />
                            </div> */}
                        </Grid>
                    </Grid>
                    <Grid sx={{ minHeight: 345, mt: 5 }}>
                        {/* <Animation isVisible animationIn='zoomIn'> */}
                        <img src="assets/element-ellipse-2_1.webp" alt="" className='position-absolute z-1' style={{ maxWidth: '100%' }} />
                        {/* </Animation> */}
                        {/* <Animation animationInDuration={2000} isVisible animationIn='fadeInLeft' animationInDelay={2000}> */}
                        <img src="assets/drone-2.webp" alt="" className='position-relative z-2' style={{ maxWidth: '100%' }} />
                        {/* </Animation> */}
                    </Grid>
                </Grid>
                <Grid container item xs={12} md={7} display={'flex'} justifyContent={'space-between'}>
                    {/* <Animation animationInDuration={2000} isVisible animationIn='fadeInLeftBig' animationInDelay={1000}> */}
                    <Grid item xs={12} md={6} my={1}>
                        {/* <div className='d-flex justify-content-center align-items-center'> */}
                        <img src="assets/icons/Vector.svg" className='p-3 bg-secondary bg-opacity-25 rounded-circle' alt="" height="80px" width="80px" />
                        {/* </div> */}
                        <Typography mt={2} className='ff-pp fw-bold' variant='h6' color="black" textAlign="start">{section.main1[lang]}</Typography>
                        <Typography className='ff-pp ts-15' color="#656565" textAlign="start">{section.sub1[lang]}</Typography>
                    </Grid>
                    {/* </Animation> */}
                    {/* <Animation animationInDuration={2000} isVisible animationIn='fadeInLeftBig'> */}
                    <Grid item xs={12} md={6} my={1}>
                        <img src="assets/icons/Group_13_(2).svg" className='bg-primary rounded-circle d-flex justify-content-center p-3' width="80px" height="80px" alt="" />

                        <Typography mt={2} className='ff-pp fw-bold' variant='h6' color="black" textAlign="start">{section.main2[lang]}</Typography>
                        <Typography className='ff-pp ts-15' color="#656565" textAlign="start">{section.sub2[lang]}</Typography>

                    </Grid>
                    {/* </Animation> */}
                    {/* <Animation animationInDuration={2000} isVisible animationIn='fadeInLeftBig' animationInDelay={1000}> */}
                    <Grid item xs={12} md={6} my={1}>
                        <div className='bg-secondary bg-opacity-25 rounded-circle d-flex justify-content-center' style={{ width: "80px", height: "80px" }} >
                            <img src="assets/icons/Group_14_(2).svg" className='m-3 ratio-1x1' alt="" />
                        </div>
                        <Typography mt={2} className='ff-pp fw-bold' variant='h6' color="black" textAlign="start">{section.main3[lang]}</Typography>
                        <Typography className='ff-pp ts-15' color="#656565" textAlign="start">{section.sub3[lang]}</Typography>
                    </Grid>
                    {/* </Animation> */}
                    {/* <Animation animationInDuration={2000} isVisible animationIn='fadeInLeftBig'> */}
                    <Grid item xs={12} md={6} my={1}>
                        <img src="assets/icons/Layer_16.svg" className='p-3 bg-secondary bg-opacity-25 rounded-circle' alt="" height="80px" width="80px" />
                        <Typography mt={2} className='ff-pp fw-bold' variant='h6' color="black" textAlign="start">{section.main4[lang]}</Typography>
                        <Typography className='ff-pp ts-15' color="#656565" textAlign="start">{section.sub4[lang]}</Typography>
                    </Grid>
                    {/* </Animation> */}
                </Grid>
            </Grid>
            <Grid container display="flex" marginY="100px" justifyContent="space-around" alignItems="center">
                <Grid item sm={12} md={5}>
                    <Typography className='ff-mi' variant='h4'>{section2.main[lang]} </Typography>
                    {/* <img src="assets/element-blur-2 1.webp" alt="" className='blur-section-3' /> */}
                </Grid>
                <Grid item sm={12} md={6}>
                    <Typography className='ff-mo' variant='h6' color="#656565">{section2.sub[lang]}</Typography>
                </Grid>
            </Grid>
        </div>
    )
}
