import { Grid, Typography } from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom'

export const AboutUsArialProject = () => {
    const lang = useSelector((state)=>state.value);
    const section = {
        title:{eng:'AERIAL PROJECT',kan:' '},
        head:{eng:'Some Beautiful Projects',kan:'  '},
        sub:{eng:'Impressive portfolio. Cutting-edge solutions. Real-world impact. Trusted leaders. Customized approach. Unmatched expertise. Success stories. Future-focused innovation.',
        kan:' .  .   .  .   .  .  . - .'},
        main1:{eng:'AERIAL DRONE',kan:' '},
        sub1:{eng:'Unleash the power of aerial drones, capture stunning aerial views, gather valuable data, and revolutionize industries.',
        kan:'   ,    ,       .'},
        main2:{eng:'4K ADVERTISE',kan:'4K '},
        sub2:{eng:'Elevate your advertising with our 4K drone. Capture stunning aerial footage for impactful campaigns. Stand out with high-quality visuals.',
        kan:' 4K    .      .     .'},
        main3:{eng:'WORK TO GO',kan:' '},
        sub3:{eng:'Get work done efficiently with our reliable drone solutions. From inspections to mapping, our drones are equipped to tackle various tasks.',
        kan:'      .   ,      .'},
        learnMore:{eng:'LEARN MORE',kan:' '}
    }
    return (
        <div>
            <Grid container display={'flex'} alignItems={'center'} mt={10}>
                <Grid item xs={12} md={4} >
                    <Typography className='ff-mi fw-bold' variant='h6' marginTop={'5'} color={'#1D69E7'}>{section.title[lang]}</Typography>
                    <Grid >
                        <Typography className='ff-mi' variant='h4' textAlign={'start'} marginY={2}>{section.head[lang]}</Typography>
                        {/* <div style={{ marginTop: -120 }} className='d-flex justify-content-start'><img src="assets/element-blur-2 1.webp" alt="" height={150} /></div> */}
                    </Grid>
                </Grid>
                <Grid item xs={12} md={8}>
                    <Typography variant='h6' color={'#656565'}>{section.sub[lang]}</Typography>
                </Grid>
            </Grid>
            <Grid container mt={2} pb={5}>
                <Grid item xs={12} sm={6} md={4} paddingX={2} marginY={2}>
                    <img src="assets/Frame_26.webp" alt="" width={'100%'} style={{ marginTop: 0, marginBottom: 40 }} />
                    <Typography variant='h6' className='ff-mi' fontWeight={'bolder'}>01. {section.main1[lang]}</Typography>
                    <Typography color={'#656565'} className='ff-mo ts-14'>{section.sub1[lang]}</Typography>
                    <Link className='fw-bold ts-12'>{section.learnMore[lang]}</Link>
                </Grid>
                <Grid item xs={12} sm={6} md={4} paddingX={2} marginY={2}>
                    <img src="assets/Frame_27.webp" alt="" width={'100%'} style={{ marginTop: 40, marginBottom: 0 }} />
                    <Typography variant='h6' className='ff-mi' fontWeight={'bolder'} marginTop={'40px'}>02. {section.main2[lang]}</Typography>
                    <Typography color={'#656565'} className='ff-mo ts-14'>{section.sub2[lang]}</Typography>
                    <Link className='fw-bold ts-12'>{section.learnMore[lang]}</Link>
                </Grid>
                <Grid item xs={12} sm={6} md={4} paddingX={2} marginY={2}>
                    <img src="assets/Frame_28.webp" alt="" width={'100%'} style={{ marginTop: 0, marginBottom: 40 }} />
                    <Typography variant='h6' className='ff-mi' fontWeight={'bolder'}>03. {section.main3[lang]}</Typography>
                    <Typography color={'#656565'} className='ff-mo ts-14'>{section.sub3[lang]}</Typography>
                    <Link className='fw-bold ts-12'>{section.learnMore[lang]}</Link>
                </Grid>
            </Grid>
        </div>
    )
}
