import { configureStore, createSlice } from "@reduxjs/toolkit";

const languageSlice = createSlice({
    name: 'laguage',
    initialState: {
        value: 'eng'
    },
    reducers: {
        changeEng: state => {
            state.value = 'eng'
        },
        changeKan: state => {
            state.value = 'kan'
        }
    }
})
export const { changeEng, changeKan } = languageSlice.actions
export default configureStore({
    reducer: languageSlice.reducer
});

